body {
  display: flex;
  flex-direction: column;

  padding: 25px;

  width: 100%;
  height: 100vh;
  background-color: #cfd8dc;
}
li {
  list-style: none;
}
h1 {
  font-size: 24px;
  font-weight: 600;
  color: #0d1024;
  margin-bottom: 20px;
  text-align: center;
}
.ghost-130 {
  height: 130px;
}
.link-pages {
  padding: 10px 20px;
  background-color: #263238;
  border: none;
  border-radius: 8px;
}
.link-pages span {
  color: #cfd8dc;
  font-size: 20px;
  font-weight: 600;
}
.link-type-dois {
  padding: 10px 20px;
  background-color: #eceff1;
  border: none;
  border-radius: 8px;
}
.link-type-dois span {
  color: #263238;
  font-size: 20px;
  font-weight: 600;
}
.footer-container {
  background-color: #00000050;

  width: 100vw;
  height: 130px;

  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  bottom: 0px;
  left: 0px;

  background: rgba(207, 216, 220, 0.5);
  backdrop-filter: blur(4px);
}
.trash-button {
  padding: 10px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #e57373;
  border: none;
  border-radius: 8px;
}
.actionButtons {
  display: flex;
  gap: 80px;
  justify-content: space-between;
  align-items: center;
}
.check-button {
  padding: 7px 14px;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #262a38;
  border: none;
  border-radius: 8px;
}
.anota-container ul {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-evenly;
  gap: 20px;
}
.check-button span {
  color: #cfd8dc;
  font-size: 20px;
  font-weight: 600;
  list-style: none;
}
.box {
  max-width: 330px;
  height: fit-content;
  padding: 20px;
  border-radius: 8px;
}
.box p {
  font-size: 14px;
  margin-bottom: 10px;
}
.box h2 {
  font-size: 20px;
}
.box:nth-child(odd) {
  background-color: #90a4ae;
}
.box:nth-child(odd) h2 {
  color: #152331;
}
.box:nth-child(odd) p {
  color: #37474f;
}
.box:nth-child(even) {
  background-color: #8e8dc1;
}
.box:nth-child(even) h2 {
  color: #1e1531;
}
.box:nth-child(even) p {
  color: #37374f;
}
/*formul√°rio*/
input {
  background-color: #eceff1;
  border: none;
  padding: 15px;
  width: 340px;
  max-width: 422px;
  border-radius: 8px;
}
textarea {
  background-color: #eceff1;
  border: none;
  padding: 15px;
  width: 340px;
  max-width: 422px;
  border-radius: 8px;
}
.input-desc {
  height: 140px;
}
form {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}
.box-flut {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
